import React from 'react';
import './Button.css';
import Brightness7Icon from '@material-ui/icons/Brightness7';
import Brightness4Icon from '@material-ui/icons/Brightness4';

const Button = ({className,val,btnClick,lightTheme}) => {
  
  let btn=`btn ${lightTheme?'btn-light':'btn-dark'}`;
  
  return (
    <div className={`btn-box ${className}`}>
      <button className={btn} value={val}
        onClick={()=>{btnClick(val)}}>
        {val}
      </button>
    </div>
  );
};

const ThemeChanger = ({val,lightTheme,btnClick}) =>{
  
  let btn=`btn ${lightTheme?'btn-light':'btn-dark'}`;
  
  let display = lightTheme?<Brightness4Icon/>:<Brightness7Icon/>;
  
  return (
    <div className={`btn-box`}>
      <button className={btn} value={val}
        onClick={()=>{btnClick(val)}}>
        {display}
      </button>
    </div>
  )
}

export default Button;
export {ThemeChanger};