import React from 'react';
import Button,{ThemeChanger} from '../Button/Button';
import './ButtonBox.css';

const ButtonBox = ({setExp,setAns,exp,setToShow,setLightTheme,lightTheme}) => {
  const btnClick = btn => {
    if (btn==='C') {
      setExp('0');
      setToShow('0');
    } else if (btn==='B') {
      setExp(exp.slice(0,exp.length-1));
      setToShow(exp.slice(0,exp.length-1));
    } else if (btn === 'T') {
      setLightTheme(old=>!old);
    } else if (btn === '=') {
      try {
        // eslint-disable-next-line
        let num = parseFloat(eval(exp));
        if (num%1 === 0) {
          setAns(num);
        } else {
          setAns(num.toFixed(4) * 1)
        }
      } catch (e) {
        setAns('Error!');
      } finally {
        setExp('');
      }
    } else if (btn === 'x') {
        setExp(old=>{
          setToShow(old+'*');
          return old+'*';
        });
    } else {
      // eslint-disable-next-line
      if(exp == 0){
        setExp(old=>{
          setToShow(btn);
          return btn;
        });
      }
      else{
        setExp(old=>{
          setToShow(old+btn);
          return old+btn;
        });
      }
    }
  }
  
  let keys=['/','7','8','9','x','4','5','6','-','1','2','3','0','.','='];
  
  const keyMaker = item => {
    return <Button val={item} btnClick={btnClick}
      key={item}
      lightTheme={lightTheme}/>
  };
  
  return (
    <div className='button-box'>
      <Button val={'C'} btnClick={btnClick}
        lightTheme={lightTheme}/>
      <Button val={'B'} btnClick={btnClick}
        lightTheme={lightTheme}/>
      
      <ThemeChanger val='T' btnClick={btnClick}
        lightTheme={lightTheme} />
        
      {keys.map(keyMaker)}
      
      <Button val='+' btnClick={btnClick} 
        className='spanned'
        lightTheme={lightTheme}/>
    </div>
  );
};

export default ButtonBox;