import React,{useState} from 'react';
import ButtonBox from './ButtonBox/ButtonBox';
import Screen from './Screen/Screen';

const App = ()=> {
  let [exp,setExp]=useState('0');
  let [ans,setAns]=useState('0');
  let [toShow,setToShow]=useState('0');
  let [lightTheme,setLightTheme]=useState(true);
  
  let title=`title ${lightTheme?'title-light':'title-dark'}`;
  
  let mainFrame=`main-frame ${lightTheme?'main-frame-light':'main-frame-dark'}`;
  
  return (window.innerWidth <= 400) ? (
    <div className={mainFrame}>
      <div className={title}>
        <h1>Calculator</h1>
      </div>
      <Screen ans={ans} toShow={toShow}
        lightTheme={lightTheme}/>
      <ButtonBox setExp={setExp} setAns={setAns}
        exp={exp} setToShow={setToShow}
        setLightTheme={setLightTheme}
        lightTheme={lightTheme}/>
    </div>
  ) : (
    <h1>This site is not for devices with Screen width more than 400px!</h1>
  );
};

export default App;