import React from 'react';
import './Screen.css';

const Screen = ({ans,toShow,lightTheme}) => {
  
  let screen = `screen ${lightTheme?'sc-light':'sc-dark'}`;
  let expCol = `exp ${lightTheme?'exp-light':'exp-dark'}`;
  let ansCol = `ans ${lightTheme?'ans-light':'ans-dark'}`
  
  return (
    <div className={screen}>
      <input type="text" className={expCol} 
        value={toShow} disabled/>
      <input type="text" className={ansCol} 
        value={ans} disabled/>
    </div>
  );
};

export default Screen;